.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.row{
  background-color: yellow;
  min-height: 12rem;
}

#preview{
  margin: auto;
  width: 100%;
  max-height: 18rem;
  
  overflow-y: auto;
  text-overflow: wrap;
  color: black;
}
